@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
/* basic
   ===================================== */

html {
  font-size: 100%;
  width: 100%;
  height: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', Helvetica, "Helvetica Neue", Arial, sans-serif;
  font-size: 16px;
  line-height: 1.52947;
  font-weight: normal;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: auto;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  direction: ltr;
  color: #323232;
  background-color: #fff;
  height: 100%;
  margin: 0;
}

#root {
  overflow-x: hidden;
  height: 100%;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

button {
  background: transparent;
  border: 0;
  padding: 0;
  outline: none;
}
.modal-mask {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0,0,0,.8);
	z-index: 5000;
}

.modal {
	position: absolute;
	top: 50%;
	left: 50%;
	text-align: center;
	background: #fff;
	width: 335px;
	margin: 0 auto;
	border-radius: 4px;
	padding: 35px;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	cursor: auto;
}

.modal canvas {
	max-width: 100%;
}

@media screen and (min-width: 320px) {
	.modal {
		width: 300px;
		padding: 25px 35px;
	}
}

.crop-title {
	display: block;
	font-size: 14px;
	color: #8e8e8e;
	margin: 12px 0;
	white-space: nowrap;
}

.crop-controls {
	margin-top: 25px;
}

.crop-controls .crop-control:first-child {
	margin-right: 15px;
}

.crop-control {
	position: relative;
	font-size: 14px;
	color: #232323;
	text-transform: uppercase;
	background: #fff;
	border: 0;
	border-radius: 4px;
	padding: 10px 25px;
	cursor: pointer;
	-webkit-transition: background 0.15s ease-in, color 0.15s ease-in;
	transition: background 0.15s ease-in, color 0.15s ease-in;
	box-shadow: 0 0 25px 1px rgba(0,0,0,0.1);
}

.crop-control:hover {
	background: #dadada;
}

