.popup-enter-active {
  -webkit-animation: IGCoreModalShow 300ms ease-in;
          animation: IGCoreModalShow 300ms ease-in;
}

@-webkit-keyframes IGCoreModalShow {
	0% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@keyframes IGCoreModalShow {
	0% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
