.auth-field {
  position: relative;
  margin-bottom: 20px;
}

.field {
  font-size: 14px;
  width: 100%;
  padding: 15px 20px;
  border: 1px solid #f2f2f2;
  border-radius: 30px;
  background: #FCFBFC;
