  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.field::-webkit-input-placeholder {
  font-size: 14px;
}

.eye {
  position: absolute;
  top: 17px;
  right: 15px;
  background: url(/travel_map/static/media/eye.12f2877a.svg) no-repeat center/contain;
  display: block;
  width: 20px;
  height: 20px;
  opacity: 0.4;
  cursor: pointer;
  -webkit-transition: opacity 0.15s ease-in;
  transition: opacity 0.15s ease-in;
}

.field:focus {
  border-color: #41A6F1;
  background: #fff;
}

.field.is-error {
  border-color: rgb(222, 53, 11);
}

.error-server {
  font-size: 12px;
  color: rgb(222, 53, 11);
  text-align: center;
  margin-top: 15px;
}

.auth-button {
  margin-top: 25px;
  text-align: center;
}

.button-send {
  display: inline-flex;
  justify-content: center;
  font-size: 14px;
  font-weight: 300;
  text-transform: uppercase;
  max-width: 100%;
  white-space: nowrap;
  vertical-align: middle;
  width: 175px;
  color: rgb(255, 255, 255);
  height: 45px;
  line-height: 45px;
  border-width: 0;
  text-decoration: none;
  border-radius: 30px;
  padding: 0 8px;
  -webkit-transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
  transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
  outline: none;
}

.button-send:enabled {
  background: #41A6F1;
  cursor: pointer;
}

.button-send:disabled {
  background-color: #cecece;
  cursor: default;
}

.button-send:enabled.button-send:hover {
  background: #75c4ff;
}

@media screen and (min-width: 320px) and (max-width: 420px) {
  .button-send {
    width: 100%;
    height: 50px;
    line-height: 50px;
  }
}

.field-valid {
  position: absolute;
  top: calc(50% - (15px / 2));
  left: -25px;
  display: block;
  width: 15px;
  height: 15px;
  background: url(/travel_map/static/media/checked.7ec9fb51.svg) no-repeat center/contain;
}

@media screen and (min-width: 320px) and (max-width: 420px) {
  .field-valid {
    left: -6%;
  }
}

.auth-bottom {
  font-size: 14px;
  color: #989898;
  text-align: center;
  margin-top: 15px;
}

@media screen and (min-width: 675px) {
  .auth-bottom{
    display: none;
  }
}

.auth-link {
  font-size: 16px;
