  position: absolute;
  left: 20px;
  bottom: -20px;
  display: block;
}

/*
Auth-animate route
=============================
*/

.example-enter {
  opacity: 0;
}

.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-exit {
  opacity: 0;
}

.example-exit-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

/*
Auth-animate error message
=============================
*/
.alert-enter {
  opacity: 0;
}
.alert-enter-active {
  opacity: 1;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
.popup-message {
	position: fixed;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	bottom: 20px;
	font-size: 12px;
	text-align: center;
	color: #323232;
	background: #f1f1f1;
	border-radius: 4px;
	width: 300px;
	padding: 10px;
	opacity: 0;
}

.popup-message::before {
	content: "";
	position: absolute;
	left: calc(50% - 12px);
	bottom: -12px;
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 12px 12px 0 12px;
	border-color: #f1f1f1 transparent transparent transparent;
}

.popup-message span {
	font-weight: 600;
}

